import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Input, Button, Card, CardBody , Modal, ModalContent, ModalHeader, ModalBody, ModalFooter, useDisclosure } from "@nextui-org/react";
let ubicacionActual = "https://remoto.rhglobal.com.ar"

function LoginForm() {
 
    const [selected, setSelected] = useState("login");
    const [loginEmail, setLoginEmail] = useState("");
    const [loginPassword, setLoginPassword] = useState("");
    const [signUpName, setSignUpName] = useState("");
    const [signUpEmail, setSignUpEmail] = useState("");
    const [signUpPassword, setSignUpPassword] = useState("");
    const [resetEmail, setResetEmail] = useState("");
    const [resetPassword, setResetPassword] = useState("");
    const [resetConfirmPassword, setResetConfirmPassword] = useState("");
    const [invalidPss, setInvalidPass] = useState(false);
    const [invalidEmail, setInvalidEmail] = useState(false);
    const [invalidName, setInvalidName] = useState(false);
    const [invalidIgual, setInvaliIgual] = useState(false);


    let ippublica
        obtenerIpPublica();


    async function obtenerIpPublica() {
        try {
            const response = await fetch('https://api.ipify.org?format=json');
            const data = await response.json();
          ippublica = data.ip
        } catch (error) {
            console.error('Error al obtener la IP:', error);
        }
    }

///////// iniciar session
const handleLoginSubmit = (e) => {
    e.preventDefault();

    const emailToLower = loginEmail.toLowerCase();

    // Validacin del formato del correo electrnico con expresin regular
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(emailToLower)) {
        console.error("El correo electrnico no es vlido.");
        setInvalidEmail(true); // Marcar el correo como invlido
        return; // Detener la ejecucin si el correo no es vlido
    }
    setInvalidEmail(false); 

    // Preparar datos para la solicitud
    const loginData = {
        correo: emailToLower,
        contrasea: loginPassword,
    };

    // Realizar solicitud POST
    fetch(ubicacionActual + '/validacioncredenciales', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(loginData),
    })
    .then(response => {
        if (!response.ok) {
            throw new Error("Respuesta del servidor no fue exitosa");
        }
        return response.json();
    })
    .then(data => {
        console.log(data);
        switch (data.respuesta) {
            case "NoExiste":
                onOpen(); // Manejar el caso de usuario no existente
                break;
            case false:
                setInvalidPass(true); // Manejar el caso de contrasea incorrecta
                break;
            case true:
                window.location.href = '/chat'; // Redirigir al usuario a /chat
                break;
            default:
                console.error("Respuesta no reconocida del servidor");
        }
    })
    .catch(error => {
        console.error("Error en la solicitud:", error);
    });
};

//////// REGISTRARSE
    
const handleSignUpSubmit = (e) => {
    e.preventDefault();

    // Expresiones regulares para las validaciones
    const regexPassword = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\W)/; // al menos una mayscula, una minscula y un smbolo
    const regexName = /^\S*$/; // no permite espacios
    const regexEmail = /^[^\s@]+@[^\s@]+\.[^\s@]+$/; // patrn bsico de correo electrnico


        // Validacin del nombre
        if (!regexName.test(signUpName)) {
            console.error("El nombre no debe contener espacios.");
            setInvalidName(true)
            return; // Termina la ejecucin si el nombre tiene espacios
        }

         // Validacin del correo electrnico
    if (!regexEmail.test(signUpEmail)) {
        setInvalidEmail(true); 
        console.error("Ingresa un correo electrnico vlido.");
        return; // Termina la ejecucin si el correo no es vlido
    }


    // Validacin de la contrasea
    if (!regexPassword.test(signUpPassword)) {
        console.error("La contrasea debe contener al menos una letra mayscula, una letra minscula y un smbolo especial.");
        setInvalidPass(true);
        return; // Termina la ejecucin si la contrasea no cumple los requisitos
    }

// Asumiendo que las validaciones ya se han pasado y los datos son correctos

// Datos del formulario
const userData = {
    name: signUpName,
    email: signUpEmail,
    password: signUpPassword
};

// Opcin de fetch para el mtodo POST
const fetchOptions = {
    method: 'POST', // Mtodo HTTP
    headers: {
        'Content-Type': 'application/json', // Indicando que el cuerpo del mensaje es un JSON
    },
    body: JSON.stringify(userData), // Convirtiendo los datos del usuario a string en formato JSON
};

fetch(ubicacionActual + '/registrodeusuario', fetchOptions)
    .then(response => {
        if (!response.ok) {
            throw new Error('Error en la solicitud');
        }
        return response.json(); // Procesando la respuesta como JSON
    })
    .then(data => {
        console.log('Success:', data); // Acciones en caso de xito, como mostrar un mensaje
        // Utilizar la URL de redireccionamiento proporcionada por el servidor para redirigir
        if(data.redirectUrl) {
            window.location.href = data.redirectUrl;
        } else {
            // Manejar el caso en que no se proporciona una URL de redireccionamiento
            onOpen()
        }
    })
    .catch((error) => {
        console.error('Error:', error); // Manejo de errores
    });
   
};


///////////////////////////////////////////// REESTABLECER CONTRASEA

const handleResetSubmit = (e) => {
    e.preventDefault();

    // Expresiones regulares para las validaciones
    const regexPassword = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\W)/; // al menos una mayscula, una minscula y un smbolo
    const regexEmail = /^[^\s@]+@[^\s@]+\.[^\s@]+$/; // patrn bsico de correo electrnico

    // Validacin del correo electrnico
    if (!regexEmail.test(resetEmail)) {
        console.error("Ingresa un correo electrnico vlido.");
        setInvalidEmail(true); 
        return; // Termina la ejecucin si el correo no es vlido
    }
    setInvalidEmail(false); 
    // Validacin de la contrasea
    if (!regexPassword.test(resetPassword)) {
        console.error("La contrasea debe contener al menos una letra mayscula, una letra minscula y un smbolo especial.");
        setInvalidPass(true);
        return; // Termina la ejecucin si la contrasea no cumple los requisitos
    }

    // Validacin de la confirmacin de la contrasea
    if (resetPassword !== resetConfirmPassword) {
        console.error("Las contraseas no coinciden.");
        setInvaliIgual(true);
        return; // Termina la ejecucin si las contraseas no coinciden
    }

    // Si todo es correcto, se procede con el reseteo de la contrasea
    fetch(  ubicacionActual + '/restartuser', {
        method: 'POST', // Mtodo HTTP para enviar datos
        headers: {
          'Content-Type': 'application/json', // Indicamos que el cuerpo de la solicitud es un JSON
        },
        body: JSON.stringify({
          email: resetEmail, // Asignamos el email ingresado
          password: resetPassword, // Asignamos la contrasea ingresada
        }),
      })
      .then(response => response.json()) // Convertimos la respuesta a JSON
      .then(data => {
        console.log('Success:', data); // Manejo exitoso de la respuesta
        if(data.rest == true){
            onOpen();
        } else {
            setInvalidEmail(true); 
        }
         // Podras llamar a onOpen aqu si deseas hacerlo tras la respuesta exitosa
      })
      .catch((error) => {
        console.error('Error:', error); // Manejo de errores de la solicitud
      });
      
};



    const {isOpen, onOpen, onOpenChange} = useDisclosure();


    return (
        <div className="flex items-center justify-center h-screen">
            <div className={`p-4 max-w-md mx-auto rounded-lg border shadow-md flex flex-col items-center translate-y-[-30%] ${selected === "login" ? "translate-y-[-30%]" : "translate-y-[-30%]"} `}>
            <Card className={`max-w-full w-[400px] ${selected === "login" ? "h-[300px]" : "h-[400px]"} bg-[#33333]`}>
                    <CardBody className="overflow-hidden">
                        <Tabs fullWidth size="md" aria-label="Tabs form" selectedKey={selected} onSelectionChange={setSelected}>
                            <Tab key="login" title="Login">
                                <form className="flex flex-col gap-4" onSubmit={handleLoginSubmit}  >
                                    <Input errorMessage={`${invalidEmail === false ? "" : "Correo invalido"}`} isRequired label="Correo" placeholder="Ingrese su Correo" type="email" value={loginEmail} onChange={(e) => setLoginEmail(e.target.value)} />
                                    <Input  errorMessage={`${invalidPss === false ? "" : "Contrasea Incorrecta"}`} isInvalid={invalidPss} isRequired label="Contrasea" placeholder="Ingrese su Contrasea" type="password" value={loginPassword} onChange={(e) => setLoginPassword(e.target.value)} />
                                    
                                </form>
                            </Tab>

                            <Tab key="sign-up" title="Registrarme">
                                <form className="flex flex-col gap-4" onSubmit={handleSignUpSubmit}>
                                    <Input errorMessage= {`${invalidName === false ? "" : "Ingrese un solo nombre sin espacios"}`} isRequired label="Nombre" placeholder="Ingrese su nombre" type="text" value={signUpName} onChange={(e) => setSignUpName(e.target.value)} />
                                    <Input errorMessage={`${invalidEmail === false ? "" : "Correo invalido"}`}  isRequired label="Correo" placeholder="Correo Electronico" type="email" value={signUpEmail} onChange={(e) => setSignUpEmail(e.target.value)} />
                                    <Input  errorMessage={`${invalidPss === false ? "" : "La contrasea necesita incluir minsculas, maysculas y un smbolo."}`}  isInvalid={invalidPss} isRequired label="Contrasea" placeholder="Ingrese una Contrasea" type="password" value={signUpPassword} onChange={(e) => setSignUpPassword(e.target.value)} />
                                </form>
                            </Tab>

                            <Tab key="Pss" title="Recuperar Contrasea">
                                <form className="flex flex-col gap-4" onSubmit={handleResetSubmit}>
                                    <Input errorMessage={`${invalidEmail === false ? "" : "Correo invalido"}`} isRequired label="Correo" placeholder="Ingrese correo electronico" type="email" value={resetEmail} onChange={(e) => setResetEmail(e.target.value)} />
                                    <Input errorMessage={`${invalidPss === false ? "" : "La contrasea necesita incluir minsculas, maysculas y un smbolo."}`}  isRequired label="Nueva contrasea" placeholder="Ingrese nueva contrasea" type="password" value={resetPassword} onChange={(e) => setResetPassword(e.target.value)} />
                                    <Input errorMessage={`${invalidIgual === false ? "" : "Las contraseas no coinciden."}`} isInvalid={invalidPss} isRequired label="Repita nueva contrasea" placeholder="Repita nueva contrasea" type="password" value={resetConfirmPassword} onChange={(e) => setResetConfirmPassword(e.target.value)} />
                                </form>
                            </Tab>
                        </Tabs>
                        <div className="flex gap-2 justify-end">
                            {selected === "login" && (
                                <Button fullWidth color="primary" onClick={handleLoginSubmit}>
                                    Ingresar
                                </Button>
                            )}
                            {selected === "sign-up" && (
                                <Button fullWidth color="primary" onClick={handleSignUpSubmit}>
                                    Registrarme
                                </Button>
                            )}
                            {selected === "Pss" && (
                                <Button fullWidth color="primary" onClick={handleResetSubmit}>
                                    Enviar
                                </Button>
                            )}
                        </div>
                    </CardBody>
                </Card>
            </div>


      <Modal 
        backdrop="opaque" 
        isOpen={isOpen} 
        onOpenChange={onOpenChange}
        radius="lg"
        classNames={{
          body: "py-6",
          backdrop: "bg-[#292f46]/50 backdrop-opacity-40",
          base: "border-[#292f46] bg-[#19172c] dark:bg-[#19172c] text-[#a8b0d3]",
          header: "border-b-[1px] border-[#292f46]",
          footer: "border-t-[1px] border-[#292f46]",
          closeButton: "hover:bg-white/5 active:bg-white/10",
        }}
      >
        <ModalContent>
        {(onClose) => (
            <>
              <ModalHeader className="flex flex-col gap-1">
              {selected === "login" && "Usuario Invalido."}
            {selected === "sign-up" && "Error Inesperado."}
            {selected === "Pss" && "Email enviado"}


              </ModalHeader>
              <ModalBody>
              <p className="mt-4">
            {selected === "login" && "Atencin: El usuario ingresado no se encuentra registrado en nuestra base de datos. Por favor, verifica la informacin e intenta nuevamente con un usuario vlido. Recuerda que si se registran ms de 5 intentos fallidos con un usuario no reconocido, tu acceso al sistema ser temporalmente suspendido por motivos de seguridad. Agradecemos tu comprensin."}
            {selected === "sign-up" && "Lamentamos los inconvenientes, pero hemos encontrado un error inesperado en nuestro sistema. Por favor, ponte en contacto con el administrador del sitio para completar el proceso de registro de manera segura. Agradecemos tu comprensin y paciencia"}
            {selected === "Pss" && "Te hemos enviado un enlace a tu correo electrnico para restablecer tu contrasea. Por favor, revisa tu bandeja de entrada y sigue las instrucciones para actualizar tu contrasea. Si no recibes el correo en los prximos minutos, verifica tambin en tu carpeta de spam. Gracias por tu paciencia!"}
        </p>
              </ModalBody>
              <ModalFooter>
                <Button color="foreground" variant="light" onPress={onClose}>
                  Close
                </Button>
                <Button className="bg-[#6f4ef2] shadow-lg shadow-indigo-500/20" onPress={onClose}>
                  Aceptar
                </Button>
              </ModalFooter>
              </>
          )}
        </ModalContent>
      </Modal>




        </div>
    );
}

export default LoginForm;
